<template>
  <a-spin :spinning="loading">
    <a-form-model
      ref="form"
      :model="entity"
      :rules="rules"
      autocomplete="off"
      layout="vertical"
    >
      <div class="card">
        <a-row :gutter="24">
          <a-col :sm="6">
            <!-- 时间窗编码 -->
            <a-form-model-item
              label="时间窗编码"
              prop="windowCode"
              ref="windowCode"
            >
              <a-input
                v-model="entity.windowCode"
                placeholder="请输入时间窗编码"
                name="windowCode"
                :disabled="!!ndoEditParams.row"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 时间窗名称 -->
            <a-form-model-item
              label="时间窗名称"
              prop="name"
              ref="name"
            >
              <a-input
                v-model="entity.name"
                placeholder="请输入时间窗名称"
                name="name"
                @change="inChange"
              ></a-input>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 时间窗类型 -->
            <a-form-model-item
              label="时间窗类型"
              prop="timeWindowType"
              ref="timeWindowType"
            >
              <a-select
                v-model="entity.timeWindowType"
                placeholder="请选择时间窗类型"
                name="timeWindowType"
              >
                <a-select-option value="生产时间窗">生产时间窗</a-select-option>
                <a-select-option value="质检时间窗">质检时间窗</a-select-option>
                <a-select-option value="物料供应时间窗">物料供应时间窗</a-select-option>
                <a-select-option value="设备维护时间窗">设备维护时间窗</a-select-option>
                <a-select-option value="车间作业时间窗">车间作业时间窗</a-select-option>
                <a-select-option value="交付时间窗">交付时间窗</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 所属部门 -->
            <a-form-model-item
              label="所属部门"
              prop="department"
              ref="department"
            >
              <a-select
                v-model="entity.department"
                placeholder="请选择所属部门"
                name="department"
              >
                <a-select-option value="生产部">生产部</a-select-option>
                <a-select-option value="质量部">质量部</a-select-option>
                <a-select-option value="物料部">物料部</a-select-option>
                <a-select-option value="设备部">设备部</a-select-option>
                <a-select-option value="计划部">计划部</a-select-option>
                <a-select-option value="物流部">物流部</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 优先级 -->
            <a-form-model-item
              label="优先级"
              prop="priority"
              ref="priority"
            >
              <a-input-number
                v-model="entity.priority"
                :min="1"
                :max="10"
                placeholder="请输入优先级"
                style="width: 100%"
                name="priority"
              ></a-input-number>
            </a-form-model-item>
          </a-col>
          <a-col :sm="6">
            <!-- 状态 -->
            <a-form-model-item
              label="状态"
              prop="status"
              ref="status"
            >
              <a-select
                v-model="entity.status"
                placeholder="请选择状态"
                name="status"
              >
                <a-select-option value="已启用">已启用</a-select-option>
                <a-select-option value="已禁用">已禁用</a-select-option>
                <a-select-option value="临时">临时</a-select-option>
                <a-select-option value="已过期">已过期</a-select-option>
              </a-select>
            </a-form-model-item>
          </a-col>
          <a-col :sm="12">
            <!-- 时间窗描述 -->
            <a-form-model-item 
              label="时间窗描述" 
              prop="description"
              ref="description"
            >
              <a-textarea
                v-model="entity.description"
                placeholder="请输入时间窗描述"
                :rows="3"
                :max-length="500"
                name="description"
              />
            </a-form-model-item>
          </a-col>
        </a-row>
      </div>

      <!-- 时间设置 -->
      <g-collapse headerName="时间设置">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="12">
              <a-form-model-item
                label="重复类型"
                prop="repeatType"
                ref="repeatType"
              >
                <a-select
                  v-model="entity.repeatType"
                  placeholder="请选择重复类型"
                  @change="handleRepeatTypeChange"
                >
                  <a-select-option value="每天">每天</a-select-option>
                  <a-select-option value="每周">每周</a-select-option>
                  <a-select-option value="每月">每月</a-select-option>
                  <a-select-option value="指定日期">指定日期</a-select-option>
                  <a-select-option value="不重复">不重复</a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>

            <a-col :sm="12" v-if="entity.repeatType === '每周'">
              <a-form-model-item
                label="每周几"
                prop="weekDays"
                ref="weekDays"
              >
                <a-checkbox-group v-model="entity.weekDays">
                  <a-checkbox value="1">周一</a-checkbox>
                  <a-checkbox value="2">周二</a-checkbox>
                  <a-checkbox value="3">周三</a-checkbox>
                  <a-checkbox value="4">周四</a-checkbox>
                  <a-checkbox value="5">周五</a-checkbox>
                  <a-checkbox value="6">周六</a-checkbox>
                  <a-checkbox value="0">周日</a-checkbox>
                </a-checkbox-group>
              </a-form-model-item>
            </a-col>

            <a-col :sm="12" v-if="entity.repeatType === '每月'">
              <a-form-model-item
                label="每月几号"
                prop="monthDays"
                ref="monthDays"
              >
                <a-select
                  v-model="entity.monthDays"
                  mode="multiple"
                  placeholder="请选择日期"
                  style="width: 100%"
                >
                  <a-select-option v-for="day in 31" :key="day" :value="day">
                    {{ day }}号
                  </a-select-option>
                </a-select>
              </a-form-model-item>
            </a-col>

            <a-col :sm="12">
              <a-form-model-item
                label="时间段"
                prop="timeRange"
                ref="timeRange"
              >
                <a-time-picker
                  v-model="entity.startTime"
                  format="HH:mm"
                  placeholder="开始时间"
                  style="width: 48%; margin-right: 4%"
                  @change="updateTimeRange"
                />
                <a-time-picker
                  v-model="entity.endTime"
                  format="HH:mm"
                  placeholder="结束时间"
                  style="width: 48%"
                  @change="updateTimeRange"
                />
              </a-form-model-item>
            </a-col>

            <a-col :sm="12">
              <a-form-model-item
                label="有效期"
                prop="effectiveDateRange"
                ref="effectiveDateRange"
              >
                <a-range-picker
                  v-model="entity.effectiveDateRange"
                  format="YYYY-MM-DD"
                  @change="updateEffectiveDate"
                  style="width: 100%"
                />
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 排除日期 -->
      <g-collapse headerName="排除日期">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-exclude-btn" type="primary" icon="plus" @click="addExcludeDate">添加排除日期</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="excludeDateColumns" 
                :dataSource="entity.excludeDates" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="date" slot-scope="text, record, index">
                  <a-date-picker
                    v-model="entity.excludeDates[index].date"
                    format="YYYY-MM-DD"
                    placeholder="选择日期"
                    style="width: 100%"
                  />
                </template>
                <template slot="reason" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.excludeDates[index].reason"
                    placeholder="请输入排除原因"
                  />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeExcludeDate(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 适用对象 -->
      <g-collapse headerName="适用对象">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="24">
              <a-button class="add-target-btn" type="primary" icon="plus" @click="addApplicableTarget">添加适用对象</a-button>
            </a-col>
            <a-col :sm="24">
              <a-table 
                :columns="applicableTargetColumns" 
                :dataSource="entity.applicableTargets" 
                :pagination="false"
                size="small"
                rowKey="id"
              >
                <template slot="targetType" slot-scope="text, record, index">
                  <a-select
                    v-model="entity.applicableTargets[index].targetType"
                    placeholder="请选择对象类型"
                    style="width: 100%"
                  >
                    <a-select-option value="设备">设备</a-select-option>
                    <a-select-option value="产品">产品</a-select-option>
                    <a-select-option value="工序">工序</a-select-option>
                    <a-select-option value="工作站">工作站</a-select-option>
                    <a-select-option value="人员">人员</a-select-option>
                  </a-select>
                </template>
                <template slot="targetName" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.applicableTargets[index].targetName"
                    placeholder="请输入对象名称"
                  />
                </template>
                <template slot="targetId" slot-scope="text, record, index">
                  <a-input
                    v-model="entity.applicableTargets[index].targetId"
                    placeholder="请输入对象ID"
                  />
                </template>
                <template slot="action" slot-scope="text, record, index">
                  <a-button 
                    type="danger" 
                    icon="delete" 
                    size="small" 
                    @click="() => removeApplicableTarget(index)"
                  />
                </template>
              </a-table>
            </a-col>
          </a-row>
        </template>
      </g-collapse>

      <!-- 元数据信息 -->
      <g-collapse headerName="详细信息">
        <template #content>
          <a-row :gutter="24">
            <a-col :sm="6">
              <!-- 创建人 -->
              <a-form-model-item label="创建人">
                <span>{{ entity.createdBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 创建时间 -->
              <a-form-model-item label="创建时间">
                <span>{{ entity.createdTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改人 -->
              <a-form-model-item label="修改人">
                <span>{{ entity.modifiedBy || '未知' }}</span>
              </a-form-model-item>
            </a-col>
            <a-col :sm="6">
              <!-- 修改时间 -->
              <a-form-model-item label="修改时间">
                <span>{{ entity.modifiedTime || '未知' }}</span>
              </a-form-model-item>
            </a-col>
          </a-row>
        </template>
      </g-collapse>
    </a-form-model>
  </a-spin>
</template>

<script>
import { GNdoPageEditBase } from "@/utils";
import moment from "moment";

export default {
  name: "CreateOrEditTimeWindow",
  mixins: [GNdoPageEditBase],
  
  data() {
    return {
      loading: false,
      entity: {
        id: undefined,
        windowCode: "",
        name: "",
        timeWindowType: "",
        department: "",
        priority: 5,
        description: "",
        repeatType: "每天",
        weekDays: [],
        monthDays: [],
        startTime: null,
        endTime: null,
        timeRange: "",
        effectiveDateRange: [],
        effectiveDate: "",
        excludeDates: [],
        applicableTargets: [],
        status: "已启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      },
      rules: {
        windowCode: [
          { required: true, message: "请输入时间窗编码", trigger: "change" },
          { pattern: /^TW\d{4}/, message: "时间窗编码格式不正确，应为TW开头加4位数字", trigger: "change" }
        ],
        name: [
          { required: true, message: "请输入时间窗名称", trigger: "change" },
          { pattern: /^[^\s]+(\s+[^\s]+)*$/, message: "时间窗名称不能包含前后空格", trigger: "change" }
        ],
        timeWindowType: [
          { required: true, message: "请选择时间窗类型", trigger: "change" }
        ],
        department: [
          { required: true, message: "请选择所属部门", trigger: "change" }
        ],
        priority: [
          { required: true, message: "请输入优先级", trigger: "change" }
        ],
        status: [
          { required: true, message: "请选择状态", trigger: "change" }
        ],
        repeatType: [
          { required: true, message: "请选择重复类型", trigger: "change" }
        ],
        weekDays: [
          { 
            required: true, 
            message: "请选择每周几", 
            trigger: "change",
            validator: (rule, value, callback) => {
              if (this.entity.repeatType === '每周' && (!value || value.length === 0)) {
                callback(new Error('请至少选择一天'));
              } else {
                callback();
              }
            }
          }
        ],
        monthDays: [
          { 
            required: true, 
            message: "请选择每月几号", 
            trigger: "change",
            validator: (rule, value, callback) => {
              if (this.entity.repeatType === '每月' && (!value || value.length === 0)) {
                callback(new Error('请至少选择一天'));
              } else {
                callback();
              }
            }
          }
        ],
        effectiveDateRange: [
          { 
            required: true, 
            message: "请选择有效期", 
            trigger: "change",
            validator: (rule, value, callback) => {
              if (!value || value.length < 2) {
                callback(new Error('请选择有效期范围'));
              } else {
                callback();
              }
            }
          }
        ]
      },
      excludeDateColumns: [
        {
          title: "日期",
          dataIndex: "date",
          key: "date",
          width: "40%",
          scopedSlots: { customRender: "date" }
        },
        {
          title: "排除原因",
          dataIndex: "reason",
          key: "reason",
          width: "50%",
          scopedSlots: { customRender: "reason" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ],
      applicableTargetColumns: [
        {
          title: "对象类型",
          dataIndex: "targetType",
          key: "targetType",
          width: "25%",
          scopedSlots: { customRender: "targetType" }
        },
        {
          title: "对象名称",
          dataIndex: "targetName",
          key: "targetName",
          width: "35%",
          scopedSlots: { customRender: "targetName" }
        },
        {
          title: "对象ID",
          dataIndex: "targetId",
          key: "targetId",
          width: "30%",
          scopedSlots: { customRender: "targetId" }
        },
        {
          title: "操作",
          key: "action",
          width: "10%",
          scopedSlots: { customRender: "action" }
        }
      ]
    };
  },
  
  methods: {
    init() {
      console.log(this.id, "初始化");
      this.loading = true;
      if (this.id) {
        // 模拟请求获取时间窗数据
        setTimeout(() => {
          this.initFormData();
          this.loading = false;
        }, 500);
      } else {
        this.reset();
        this.loading = false;
      }
    },
    
    resetFormData() {
      this.entity = {
        id: undefined,
        windowCode: "",
        name: "",
        timeWindowType: "",
        department: "",
        priority: 5,
        description: "",
        repeatType: "每天",
        weekDays: [],
        monthDays: [],
        startTime: null,
        endTime: null,
        timeRange: "",
        effectiveDateRange: [],
        effectiveDate: "",
        excludeDates: [],
        applicableTargets: [],
        status: "已启用",
        createdBy: "",
        createdTime: "",
        modifiedBy: "",
        modifiedTime: "",
      };
    },
    
    initFormData() {
      if (this.ndoEditParams.row) {
        const row = this.ndoEditParams.row;
        
        // 处理时间段
        let startTime = null;
        let endTime = null;
        if (row.timeRange) {
          const times = row.timeRange.split(' - ');
          if (times.length === 2) {
            startTime = moment(times[0], 'HH:mm');
            endTime = moment(times[1], 'HH:mm');
          }
        }
        
        // 处理有效期
        let effectiveDateRange = [];
        if (row.effectiveDate) {
          const dates = row.effectiveDate.split(' 至 ');
          if (dates.length === 2) {
            effectiveDateRange = [
              moment(dates[0], 'YYYY-MM-DD'),
              moment(dates[1], 'YYYY-MM-DD')
            ];
          }
        }
        
        this.entity = {
          ...this.entity,
          ...row,
          startTime,
          endTime,
          effectiveDateRange,
          // 确保数组被正确初始化
          weekDays: row.weekDays || [],
          monthDays: row.monthDays || [],
          excludeDates: row.excludeDates || [],
          applicableTargets: row.applicableTargets || []
        };
      }
    },
    
    async save() {
      // 更新时间范围和有效期字段
      this.updateTimeRange();
      this.updateEffectiveDate();
      
      // 保存校验
      let res = await this.checkError();
      if (res) {
        this.loading = true;
        // 模拟保存操作
        setTimeout(() => {
          this.loading = false;
          this.saveSuccess(this.entity.name);
          this.handleSaveEndBackView(this.id);
          this.refreshParent({
            reset: null,
          });
        }, 500);
      }
    },
    
    inChange(e) {
      console.log(e.target.value);
    },
    
    // 处理重复类型变更
    handleRepeatTypeChange(value) {
      // 清空相关字段
      if (value !== '每周') {
        this.entity.weekDays = [];
      }
      
      if (value !== '每月') {
        this.entity.monthDays = [];
      }
    },
    
    // 更新时间段
    updateTimeRange() {
      if (this.entity.startTime && this.entity.endTime) {
        const startStr = moment(this.entity.startTime).format('HH:mm');
        const endStr = moment(this.entity.endTime).format('HH:mm');
        this.entity.timeRange = `${startStr} - ${endStr}`;
      } else {
        this.entity.timeRange = '';
      }
    },
    
    // 更新有效期
    updateEffectiveDate() {
      if (this.entity.effectiveDateRange && this.entity.effectiveDateRange.length === 2) {
        const startStr = moment(this.entity.effectiveDateRange[0]).format('YYYY-MM-DD');
        const endStr = moment(this.entity.effectiveDateRange[1]).format('YYYY-MM-DD');
        this.entity.effectiveDate = `${startStr} 至 ${endStr}`;
      } else {
        this.entity.effectiveDate = '';
      }
    },
    
    // 添加排除日期
    addExcludeDate() {
      this.entity.excludeDates.push({
        id: Date.now(), // 使用时间戳作为临时ID
        date: null,
        reason: ""
      });
    },
    
    // 删除排除日期
    removeExcludeDate(index) {
      this.entity.excludeDates.splice(index, 1);
    },
    
    // 添加适用对象
    addApplicableTarget() {
      this.entity.applicableTargets.push({
        id: Date.now(), // 使用时间戳作为临时ID
        targetType: "",
        targetName: "",
        targetId: ""
      });
    },
    
    // 删除适用对象
    removeApplicableTarget(index) {
      this.entity.applicableTargets.splice(index, 1);
    }
  },
};
</script>

<style lang="scss" scoped>
.card {
  margin-bottom: 16px;
  padding: 16px;
  background: #fff;
  border-radius: 4px;
  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.05);
}

.add-exclude-btn,
.add-target-btn {
  margin-bottom: 16px;
}
</style> 